<template>
<div class="register-page">
  <loginLogo/>
  <div class="register">
    <div class="register-head">
      <span></span>
    </div>
    <el-form :model="registerForm" status-icon :rules="rules" ref="registerForm" label-width="80px" class="demo-ruleForm">
      <el-form-item prop="username" label="">
        <el-input v-model="registerForm.username" placeholder=""></el-input>
      </el-form-item>
      <el-form-item prop="password" label="">
        <el-input type="password" placeholder="" v-model="registerForm.password"></el-input>
      </el-form-item>
      <el-form-item prop="sex" label="">
        <el-radio-group v-model="registerForm.sex">
          <el-radio :label="0"></el-radio>
          <el-radio :label="1"></el-radio>
        </el-radio-group>
      </el-form-item>
      <el-form-item prop="phoneNum" label="" >
        <el-input  placeholder="" v-model="registerForm.phoneNum"></el-input>
      </el-form-item>
      <el-form-item prop="email" label="">
        <el-input v-model="registerForm.email" placeholder=""></el-input>
      </el-form-item>
      <el-form-item prop="birth" label="">
        <el-date-picker type="date" placeholder="" v-model="registerForm.birth" style="width: 100%;"></el-date-picker>
      </el-form-item>
      <el-form-item prop="introduction" label="">
        <el-input  type="textarea" placeholder="" v-model="registerForm.introduction" ></el-input>
      </el-form-item>
      <el-form-item prop="location" label="">
        <el-select v-model="registerForm.location" placeholder="">
          <el-option v-for="item in cities" :key="item.value" :label="item.label" :value="item.value"></el-option>
        </el-select>
      </el-form-item>
      <div class="login-btn">
        <el-button @click="goback(-1)"></el-button>
        <el-button type="primary" @click="register"></el-button>
      </div>
    </el-form>
  </div>
</div>
</template>

<script>
import axios from 'axios'
import loginLogo from '../components/LoginLogo'

export default {
  name: 'register-page',
  components: {
    loginLogo
  },
  data: function () {
    return {
      registerForm: { // 
        username: '',
        password: '',
        sex: '',
        phoneNum: '',
        email: '',
        birth: '',
        introduction: '',
        location: ''
      },
      rules: {
        username: [
          { required: true, trigger: 'blur' }
        ],
        password: [
          { required: true, trigger: 'blur' }
        ],
        sex: [
          { required: true, message: '', trigger: 'change' }
        ],
        phoneNum: [
          { essage: '', trigger: 'blur' }
        ],
        email: [
          { message: '', trigger: 'blur' },
          { type: 'email', message: '', trigger: ['blur', 'change'] }
        ],
        birth: [
          { required: true, type: 'date', message: '', trigger: 'change' }
        ],
        introduction: [
          { message: '', trigger: 'blur' }
        ],
        location: [
          { message: '', trigger: 'change' }
        ]
      },
      cities: [{
        value: '',
        label: ''
      }, {
        value: '',
        label: ''
      }, {
        value: '',
        label: ''
      }, {
        value: '',
        label: ''
      }, {
        value: '',
        label: ''
      }, {
        value: '',
        label: ''
      }, {
        value: '',
        label: ''
      }, {
        value: '',
        label: ''
      }, {
        value: '',
        label: ''
      }, {
        value: '',
        label: ''
      }, {
        value: '',
        label: ''
      }, {
        value: '',
        label: ''
      }, {
        value: '',
        label: ''
      }, {
        value: '',
        label: ''
      }, {
        value: '',
        label: ''
      }, {
        value: '',
        label: ''
      }, {
        value: '',
        label: ''
      }, {
        value: '',
        label: ''
      }, {
        value: '',
        label: ''
      }, {
        value: '',
        label: ''
      }, {
        value: '',
        label: ''
      }, {
        value: '',
        label: ''
      }, {
        value: '',
        label: ''
      }, {
        value: '',
        label: ''
      }, {
        value: '',
        label: ''
      }, {
        value: '',
        label: ''
      }, {
        value: '',
        label: ''
      }, {
        value: '',
        label: ''
      }, {
        value: '',
        label: ''
      }, {
        value: '',
        label: ''
      }, {
        value: '',
        label: ''
      }, {
        value: '',
        label: ''
      }, {
        value: '',
        label: ''
      }, {
        value: '',
        label: ''
      }]
    }
  },
  methods: {
    register () {
      let _this = this
      let d = _this.registerForm.birth
      var datetime = d.getFullYear() + '-' + (d.getMonth() + 1) + '-' + d.getDate()
      var params = new URLSearchParams()
      params.append('username', _this.registerForm.username)
      params.append('password', _this.registerForm.password)
      params.append('sex', _this.registerForm.sex)
      params.append('phone_num', _this.registerForm.phoneNum)
      params.append('email', _this.registerForm.email)
      params.append('birth', datetime)
      params.append('introduction', _this.registerForm.introduction)
      params.append('location', _this.registerForm.location)
      params.append('avator', '/img/user.jpg')
      axios.post(`${_this.$store.state.HOST}/api/signup`, params)
        .then(response => {
          if (response.data.code === 1) {
            _this.$notify({
              title: '',
              type: 'success'
            })
            setTimeout(function () {
              _this.$router.push({path: '/'})
            }, 2000)
          } else {
            _this.$notify({
              title: '',
              type: 'error'
            })
          }
        })
        .catch(failResponse => {})
    },
    goback (index) {
      this.$router.go(index)
    }
  }
}
</script>

<style scoped>
.register{
  position: absolute;
  top:150px;
  background-color: white;
  border-radius: 10px;
  width: 350px;
  height: 580px;
  margin-left: 750px;
  padding: 30px 30px;
}

.register-head {
  text-align: center;
  margin-bottom: 10px;
  font-size: 20px;
  font-weight: 600;
}

.login-btn {
  display: flex;
  justify-content: space-between;
}

.login-btn button{
  display: block;
  width: 50%;
}

.local {
  position: absolute;
  width: 270px;
  top: 550px;
  margin-left: 80px;
}

</style>



