<template>
  <div>
    <div class="my-bg"></div>
    <div class="my-body">
      <div class="content">
        <p class="title"></p>
        <hr/>
        <div class="personal">
          <el-form :model="registerForm" class="demo-ruleForm" label-width="80px">
            <el-form-item prop="username" label="">
              <el-input v-model="registerForm.username" placeholder=""></el-input>
            </el-form-item>
            <el-form-item prop="password" label="">
              <el-input type="password" placeholder="" v-model="registerForm.password"></el-input>
            </el-form-item>
            <el-form-item label="">
              <el-radio-group v-model="registerForm.sex">
                <el-radio :label="0"></el-radio>
                <el-radio :label="1"></el-radio>
              </el-radio-group>
            </el-form-item>
            <el-form-item prop="phoneNum" label="">
              <el-input  placeholder="" v-model="registerForm.phoneNum" ></el-input>
            </el-form-item>
            <el-form-item prop="email" label="">
              <el-input v-model="registerForm.email" placeholder=""></el-input>
            </el-form-item>
            <el-form-item prop="birth" label="">
              <el-date-picker type="date" placeholder="" v-model="registerForm.birth" style="width: 100%;"></el-date-picker>
            </el-form-item>
            <el-form-item prop="introduction" label="">
              <el-input  type="textarea" placeholder="" v-model="registerForm.introduction" ></el-input>
            </el-form-item>
            <el-form-item prop="location" label="">
              <el-select v-model="registerForm.location" placeholder="">
                <el-option
                  v-for="item in cities"
                  :key="item.value"
                  :label="item.label"
                  :value="item.value">
                </el-option>
              </el-select>
            </el-form-item>
          </el-form>
        <div class="btn">
          <div @click="saveMsg()"></div>
          <div @click="goback"></div>
        </div>
        </div>
      </div>
    </div>
</div>
</template>

<script>
import axios from 'axios'
import { mapGetters } from 'vuex'

export default {
  name: 'personal',
  data: function () {
    return {
      registerForm: { // 
        username: '',
        password: '',
        sex: '',
        phoneNum: '',
        email: '',
        birth: '',
        introduction: '',
        location: ''
      },
      cities: [{
        value: '',
        label: ''
      }, {
        value: '',
        label: ''
      }, {
        value: '',
        label: ''
      }, {
        value: '',
        label: ''
      }, {
        value: '',
        label: ''
      }, {
        value: '',
        label: ''
      }, {
        value: '',
        label: ''
      }, {
        value: '',
        label: ''
      }, {
        value: '',
        label: ''
      }, {
        value: '',
        label: ''
      }, {
        value: '',
        label: ''
      }, {
        value: '',
        label: ''
      }, {
        value: '',
        label: ''
      }, {
        value: '',
        label: ''
      }, {
        value: '',
        label: ''
      }, {
        value: '',
        label: ''
      }, {
        value: '',
        label: ''
      }, {
        value: '',
        label: ''
      }, {
        value: '',
        label: ''
      }, {
        value: '',
        label: ''
      }, {
        value: '',
        label: ''
      }, {
        value: '',
        label: ''
      }, {
        value: '',
        label: ''
      }, {
        value: '',
        label: ''
      }, {
        value: '',
        label: ''
      }, {
        value: '',
        label: ''
      }, {
        value: '',
        label: ''
      }, {
        value: '',
        label: ''
      }, {
        value: '',
        label: ''
      }, {
        value: '',
        label: ''
      }, {
        value: '',
        label: ''
      }, {
        value: '',
        label: ''
      }, {
        value: '',
        label: ''
      }, {
        value: '',
        label: ''
      }]
    }
  },
  computed: {
    ...mapGetters([
      'userId'
    ])
  },
  mounted () {
    this.getMsg(this.userId)
  },
  methods: {
    getMsg (id) {
      let _this = this
      axios.get(`${_this.$store.state.HOST}/commentOfConsumer?id=${id}`)
        .then(response => {
          _this.registerForm.username = response.data[0].username
          _this.registerForm.password = response.data[0].password
          _this.registerForm.sex = response.data[0].sex
          _this.registerForm.phoneNum = response.data[0].phoneNum
          _this.registerForm.email = response.data[0].email
          _this.registerForm.birth = response.data[0].birth
          _this.registerForm.introduction = response.data[0].introduction
          _this.registerForm.location = response.data[0].location
          _this.registerForm.avator = response.data[0].avator
        })
    },
    goback () {
      this.$router.go(-1)
    },
    saveMsg () {
      let _this = this
      let d = new Date(_this.registerForm.birth)
      var datetime = d.getFullYear() + '-' + (d.getMonth() + 1) + '-' + d.getDate()
      var params = new URLSearchParams()
      params.append('id', _this.userId)
      params.append('username', _this.registerForm.username)
      params.append('password', _this.registerForm.password)
      params.append('sex', _this.registerForm.sex)
      params.append('phone_num', _this.registerForm.phoneNum)
      params.append('email', _this.registerForm.email)
      params.append('birth', datetime)
      params.append('introduction', _this.registerForm.introduction)
      params.append('location', _this.registerForm.location)
      params.append('avator', '/img/user.jpg')
      axios.post(`${_this.$store.state.HOST}/api/updateUserMsgs`, params)
        .then(res => {
          if (res.data.code === 1) {
            _this.showError = false
            _this.showSuccess = true
            this.$store.commit('setUsername', _this.registerForm.username)
            window.localStorage.setItem('username', JSON.stringify(_this.registerForm.username))
            this.$notify.success({
              title: '',
              showClose: true
            })
            setTimeout(function () {
              _this.$router.go(-1)
            }, 2000)
          } else {
            _this.showSuccess = false
            _this.showError = true
            this.$notify.error({
              title: '',
              showClose: true
            })
          }
        })
        .catch(failResponse => {})
    }
  }
}
</script>

<style scoped>
.my-bg {
  width: 100%;
  height: 100px;
  background-color: #93d2f8;
}

.my-body{
  position: relative;
  padding: 0 100px;
}

.content {
  width: 60%;
  margin:50px auto;
  background-color: white;
  border-radius: 10px;
  padding-bottom: 30px;
}

.content p {
  height: 50px;
  line-height: 50px;
  padding-left: 20px;
  font-size: 20px;
  font-weight: 600;
  color: black;
}

.personal {
  padding-right: 50px;
  padding-top: 40px;
}

.btn {
  width: 100%;
  height: 40px;
  display: flex;
  align-items: center;
  justify-content: center;
  cursor: pointer;
}

.btn div {
  display: inline-block;
  width: 100px;
  height: 30px;
  line-height: 30px;
  text-align: center;
  background-color: #30a4fc;
  margin: 0 20px;
  color: white;
}
</style>



