<template>
  <div class="song-audio">
    <audio :src='url' controls="controls" ref="player" preload="true"  @canplay="startPlay" @timeupdate="timeupdate" @ended="ended">
      <!--1controlspreload2canplaytimeupdateended3play()pause() -->
      <!--controls///-->
      <!--preload-->
      <!--canplay/-->
      <!--timeupdate-->
      <!--ended-->
    </audio>
  </div>
</template>

<script>
import { mapGetters } from 'vuex'

export default {
  name: 'song-audio',
  computed: {
    ...mapGetters([
      'id', // id
      'url', // 
      'listOfSongs', // 
      'isPlay', // 
      'curTime', // 
      'changeTime', // 
      'autoNext' // 
    ])
  },
  watch: {
    // 
    isPlay: function () {
      this.togglePlay()
    },
    // 
    changeTime: function () {
      let player = this.$refs.player
      player.currentTime = this.changeTime
    }
  },
  methods: {
    // /
    togglePlay () {
      let player = this.$refs.player
      if (this.isPlay) {
        player.play()
      } else {
        player.pause()
      }
    },
    // 
    startPlay () {
      let player = this.$refs.player
      //  
      this.$store.commit('setDuration', player.duration)
      window.sessionStorage.setItem('duration', JSON.stringify(player.duration))
      //  
      player.play()
      this.$store.commit('setIsPlay', true)
      window.sessionStorage.setItem('isPlay', JSON.stringify(true))
    },
    // 
    timeupdate () {
      let player = this.$refs.player
      this.$store.commit('setCurTime', player.currentTime)
      window.sessionStorage.setItem('curTime', JSON.stringify(player.currentTime))
    },
    // 
    ended () {
      this.$store.commit('setIsPlay', false)
      window.sessionStorage.setItem('isPlay', JSON.stringify(false))
      this.$store.commit('setCurTime', 0)
      window.sessionStorage.setItem('curTime', JSON.stringify(0))
      this.$store.commit('setAutoNext', !this.autoNext)
      window.sessionStorage.setItem('autoNext', JSON.stringify(!this.autoNext))
    }
  }
}
</script>

<style>
audio {
  display: none;
}
</style>



